import React from 'react'
import styled from 'styled-components'
import { Text, Flex, Heading, IconButton, ArrowBackIcon } from '@pancakeswap/uikit'
import { Link } from 'react-router-dom'
import Settings from './Settings'
import Transactions from './Transactions'
import QuestionHelper from '../QuestionHelper'

interface Props {
  title: string
  subtitle: string
  helper?: string
  backTo?: string
  noConfig?: boolean
}

const AppHeaderContainer = styled(Flex)`
  align-items: center;
  justify-content: center;
  padding: 25px 15px;
  width: 100%;
  border-top: 4px solid transparent;
  border-bottom:1px #8080804f solid;
`

const AppHeader: React.FC<Props> = ({ title, subtitle, helper, backTo, noConfig = false }) => {
  return (
    <AppHeaderContainer>
      <Flex flexDirection="column" width="100%" position="relative">
        <Flex flexDirection="row" alignItems="center" justifyContent="space-between">
          <Heading as="h2" style={{ textAlign: `${title === 'Swap' ? 'center' : 'left'}`, fontSize: "24px", flex: 2 }}>
            {title}
          </Heading>
          {!noConfig && (
            <Flex style={{ position: 'absolute', right: '15px' }}>
              <Settings />
              <Transactions />
            </Flex>
          )}
        </Flex>

        <Text style={{ color: "#7A6EAA", margin: 'auto', marginTop: '10px' }} fontSize="14px">
          {subtitle}
        </Text>
      </Flex>
    </AppHeaderContainer>
  )
}

export default AppHeader
